import gsap from 'gsap';
import React, { useCallback, useEffect, useMemo, useRef, VFC } from 'react';
import * as THREE from 'three';
import { useTexture } from '@react-three/drei';
import { useFrame } from '@react-three/fiber';
import { rotateX, rotateY, rotateZ } from '../../modules/glsl';
import { ImageFileNames, transitionState } from '../../modules/store';

export const PlaneFragments: VFC = () => {
	const meshRef = useRef<THREE.InstancedMesh>(null)

	// --------------------------------------------
	// import textures
	// eslint-disable-next-line react-hooks/rules-of-hooks
	const textures = ImageFileNames.map(name => useTexture(process.env.PUBLIC_URL + `/assets/images/${name}.jpg`))
	textures.forEach(texture => (texture.encoding = THREE.sRGBEncoding))

	// --------------------------------------------
	// define size
	const [widthSegments, heightSegments] = [16, 9]
	const amount = widthSegments * heightSegments

	// --------------------------------------------
	// create plane positions & absolute uv
	const { positions, uv } = useMemo(() => {
		const positions = []
		const uv = []
		const offset = { x: widthSegments / 2 - 0.5, y: heightSegments / 2 - 0.5 }

		for (let iw = 0; iw < widthSegments; iw++) {
			for (let ih = 0; ih < heightSegments; ih++) {
				positions.push(iw - offset.x, ih - offset.y, 0)
				uv.push(iw / widthSegments, ih / heightSegments)
			}
		}
		return { positions, uv }
	}, [heightSegments, widthSegments])

	// --------------------------------------------
	// set instancedMesh matrix
	useEffect(() => {
		const object = new THREE.Object3D()

		for (let i = 0; i < amount; i++) {
			object.position.set(positions[i * 3], positions[i * 3 + 1], positions[i * 3 + 2])
			object.updateMatrix()

			meshRef.current!.setMatrixAt(i, object.matrix)
		}
		meshRef.current!.instanceMatrix.needsUpdate = true
	}, [amount, positions])

	// --------------------------------------------
	// create plane fragment paramaters texture
	const dataTexture = useMemo(() => {
		const size = widthSegments * heightSegments
		const data = new Float32Array(4 * size)
		for (let i = 0; i < size; i++) {
			// skew
			data[i * 4 + 0] = Math.random() * 255
			// scale
			data[i * 4 + 1] = Math.random() * 255
			// radius
			data[i * 4 + 2] = Math.random() * 0.6 * 255
			const rand = Math.random()
			if (0.95 < rand) {
				data[i * 4 + 2] = rand * 255
			}
			// angle
			data[i * 4 + 3] = Math.random() * 255
		}
		const texture = new THREE.DataTexture(data, widthSegments, heightSegments, THREE.RGBAFormat, THREE.FloatType)
		texture.needsUpdate = true
		return texture
	}, [widthSegments, heightSegments])

	// --------------------------------------------
	// create shader
	const shader: THREE.Shader = {
		uniforms: {
			u_time: { value: 0 },
			u_progress: { value: 0 },
			u_textureProgress: { value: 0 },
			u_dataTexture: { value: dataTexture },
			u_texture: { value: textures[0] },
			u_prevTexture: { value: textures[0] },
			u_segments: { value: [widthSegments, heightSegments] },
			u_uvScale: { value: new THREE.Vector2() }
		},
		vertexShader: vertexShader,
		fragmentShader: fragmentShader
	}

	// --------------------------------------------
	// texture uv scale update
	const updateTextureAspect = useCallback(
		(texture: THREE.Texture) => {
			const imageAspect = texture.image.width / texture.image.height
			const aspect = widthSegments / heightSegments

			if (aspect < imageAspect) {
				shader.uniforms.u_uvScale.value.set(aspect / imageAspect, 1)
			} else {
				shader.uniforms.u_uvScale.value.set(1, imageAspect / aspect)
			}
		},
		[heightSegments, shader.uniforms.u_uvScale, widthSegments]
	)

	useEffect(() => {
		updateTextureAspect(textures[0])
	}, [textures, updateTextureAspect])

	// --------------------------------------------
	// animation
	const animation = () => {
		const tl = gsap.timeline({
			onComplete: () => {
				transitionState.enabledHandler = true
			}
		})
		tl.to(shader.uniforms.u_progress, {
			value: 1,
			duration: 0.5,
			ease: 'power3.in',
			onComplete: () => {
				shader.uniforms.u_prevTexture.value = textures[transitionState.prevIndex]
				shader.uniforms.u_texture.value = textures[transitionState.textureIndex]
				shader.uniforms.u_textureProgress.value = 0
				updateTextureAspect(textures[transitionState.textureIndex])
			}
		})
		tl.to(shader.uniforms.u_textureProgress, { value: 1, duration: 0.5, ease: 'none' })
		tl.to(shader.uniforms.u_progress, { value: 0, duration: 0.5, ease: 'expo.out' })
	}

	// frame loop
	const vec3 = new THREE.Vector3()
	useFrame(({ mouse, viewport }) => {
		// update uniforms
		shader.uniforms.u_time.value += 0.005

		// mouse motion
		const x = (mouse.x * viewport.width) / 2
		const y = (mouse.y * viewport.height) / 2
		vec3.lerp(new THREE.Vector3(-x, -y, 50), 0.2)
		meshRef.current!.lookAt(vec3)

		// animation
		if (transitionState.enabledAnimation) {
			animation()
			transitionState.enabledAnimation = false
		}
	})

	return (
		<instancedMesh ref={meshRef} args={[undefined, undefined, amount]}>
			<planeGeometry args={[1, 1, 10, 10]}>
				<instancedBufferAttribute attachObject={['attributes', 'a_pos']} args={[Float32Array.from(positions), 3]} />
				<instancedBufferAttribute attachObject={['attributes', 'a_uv']} args={[Float32Array.from(uv), 2]} />
			</planeGeometry>
			<shaderMaterial args={[shader]} side={THREE.DoubleSide} wireframe={false} />
		</instancedMesh>
	)
}

// ========================================================
// shader

const vertexShader = `
uniform float u_progress;
uniform sampler2D u_dataTexture;
uniform float u_time;
attribute vec3 a_pos;
attribute vec2 a_uv;
varying vec2 v_uv;
varying vec2 v_normUv;

const float PI = 3.14159265358979;
const vec3 BASIC_VECTOR = normalize(vec3(1.0, 0.0, 0.0));

${rotateX}
${rotateY}
${rotateZ}

float scaleParam(float p, float min, float max) {
	return p * (max - min) + min;
}

void main() {
	v_normUv = a_uv;
	v_uv = uv;

  vec3 pos = position.xyz;
  vec4 globalPosition = instanceMatrix * vec4(pos, 1.0);
  vec3 gPos = globalPosition.xyz;

	vec4 params = texture2D(u_dataTexture, a_uv + 0.001);
	params /= 255.0;

	float skew = scaleParam(params.x, 1.0, 1.5);
	float scale = scaleParam(params.y, 0.1, 0.2);
	float radius = scaleParam(params.z, 1.0, 4.0);
	float angle = scaleParam(params.w, -PI / 2.0, PI / 2.0);
	float speed = scaleParam(params.x, 20.0, 30.0);
	float angle2 = scaleParam(params.y, -PI / 2.0, PI / 2.0);

	// -------------------------------
  // to center
	gPos -= a_pos;
	// -------------------------------
  // z+1
  gPos.z += 1.0;
	// -------------------------------
  // skew
  gPos.x += gPos.y * skew;
	// -------------------------------
  // scale
  gPos.y *= scale;
	// -------------------------------
  // fit sphere
  gPos = normalize(gPos) * radius;
	// -------------------------------
  // rotate
	gPos = rotateY(gPos, u_time * speed);
  gPos = rotateX(gPos, angle + u_time * params.x);
	gPos = rotateZ(gPos, angle2 + u_time * params.z);
	// -------------------------------
	// wave
	globalPosition.z += 0.2 * sin((globalPosition.x + globalPosition.y) + u_time * 10.0) * (1.0 - u_progress);
	// -------------------------------
	// progress
	float len = length(pos);
	len = smoothstep(0.0, 0.707, len);
	len = 1.0 - len;
	if (0.1 < u_progress) {
		len += u_progress;
	}
	float progress = clamp(u_progress * len, 0.0, 1.0);
	progress = mix(progress, u_progress, u_progress);
	gPos = mix(globalPosition.xyz, gPos, progress);
  
	gl_Position = projectionMatrix * viewMatrix * modelMatrix * vec4(gPos, globalPosition.w);
}
`

const fragmentShader = `
uniform float u_textureProgress;
uniform sampler2D u_texture;
uniform sampler2D u_prevTexture;
uniform vec2 u_segments;
uniform vec2 u_uvScale;
varying vec2 v_uv;
varying vec2 v_normUv;

void main() {
	vec2 uv = v_uv / u_segments + v_normUv;
	uv = (uv - 0.5) * u_uvScale + 0.5;
	vec4 tex = texture2D(u_texture, uv);
	vec4 prevTex = texture2D(u_prevTexture, uv);
	vec3 color = mix(prevTex.rgb, tex.rgb, u_textureProgress);

  gl_FragColor = vec4(color, tex.a);
}
`
