import React, { VFC } from 'react';
import { css } from '@emotion/css';

export const Background: VFC = () => {
	const line1 = [...Array(10)].flatMap(() => ['Three.js', 'React Three Fiber', 'GLSL'])
	const line2 = [...Array(10)].flatMap(() => ['unshift', 'WLOP', 'Nemutas'])

	return (
		<div className={styles.container}>
			<div className={styles.textContainer}>
				<TextLine line={line1} highlightText="Three.js" i={0.05} />
				<TextLine line={line2} highlightText="unshift" i={0.1} />
				<TextLine line={line1} highlightText="React Three Fiber" i={0.05} />
				<TextLine line={line2} highlightText="WLOP" i={0.1} />
				<TextLine line={line1} highlightText="GLSL" i={0.05} />
				<TextLine line={line2} highlightText="Nemutas" i={0.1} />

				<TextLine line={line1} highlightText="Three.js" i={0.05} />
				<TextLine line={line2} highlightText="unshift" i={0.1} />
				<TextLine line={line1} highlightText="React Three Fiber" i={0.05} />
				<TextLine line={line2} highlightText="WLOP" i={0.1} />
				<TextLine line={line1} highlightText="GLSL" i={0.05} />
				<TextLine line={line2} highlightText="Nemutas" i={0.1} />

				<TextLine line={line1} highlightText="Three.js" i={0.05} />
				<TextLine line={line2} highlightText="unshift" i={0.1} />
				<TextLine line={line1} highlightText="React Three Fiber" i={0.05} />
				<TextLine line={line2} highlightText="WLOP" i={0.1} />
				<TextLine line={line1} highlightText="GLSL" i={0.05} />
				<TextLine line={line2} highlightText="Nemutas" i={0.1} />
			</div>
		</div>
	)
}

type TextLineProps = {
	line: string[]
	highlightText?: string
	i: number
}
const TextLine: VFC<TextLineProps> = props => {
	const { line, highlightText, i } = props

	return (
		<div className={styles.translate(i)}>
			{line.map((text, i) => (
				<span key={i}>
					<span className={text === highlightText ? styles.fillText : undefined}>{text}</span>
					<span> | </span>
				</span>
			))}
		</div>
	)
}

const styles = {
	container: css`
		position: absolute;
		top: 0;
		left: 0;
		width: 300%;
		height: 100vh;
		background-color: #121212;
	`,
	textContainer: css`
		user-select: none;
		pointer-events: none;
		color: transparent;
		font-size: 7rem;
		white-space: nowrap;
		font-weight: 700;
		-webkit-text-stroke: 2px #484848;
		transform: skewY(340deg) translateY(-1300px);
	`,
	fillText: css`
		color: #484848;
	`,
	translate: (i: number) => css`
		text-shadow: calc(var(--x) * ${i} * 2) 0px 0 rgba(255, 255, 255, 0.05);
		transform: translateX(calc(0% - var(--x) * ${i} - 25%));
	`
}
